@file:Suppress("DEPRECATION", "OVERRIDE_DEPRECATION")

package com.example.redwood.emojisearch.widget

import app.cash.redwood.Modifier
import app.cash.redwood.RedwoodCodegenApi
import app.cash.redwood.layout.widget.RedwoodLayoutWidgetFactory
import app.cash.redwood.layout.widget.RedwoodLayoutWidgetFactoryOwner
import app.cash.redwood.lazylayout.widget.RedwoodLazyLayoutWidgetFactory
import app.cash.redwood.lazylayout.widget.RedwoodLazyLayoutWidgetFactoryOwner
import app.cash.redwood.widget.WidgetFactoryOwner
import app.cash.redwood.widget.WidgetSystem
import com.example.redwood.emojisearch.modifier.Reuse
import kotlin.Any
import kotlin.OptIn
import kotlin.Suppress
import kotlin.experimental.ExperimentalObjCName
import kotlin.native.ObjCName

@OptIn(
  ExperimentalObjCName::class,
  RedwoodCodegenApi::class,
)
@ObjCName(
  "EmojiSearchWidgetSystem",
  exact = true,
)
public class EmojiSearchWidgetSystem<W : Any>(
  override val EmojiSearch: EmojiSearchWidgetFactory<W>,
  override val RedwoodLayout: RedwoodLayoutWidgetFactory<W>,
  override val RedwoodLazyLayout: RedwoodLazyLayoutWidgetFactory<W>,
) : WidgetSystem<W>,
    EmojiSearchWidgetFactoryOwner<W>,
    RedwoodLayoutWidgetFactoryOwner<W>,
    RedwoodLazyLayoutWidgetFactoryOwner<W> {
  override fun apply(`value`: W, element: Modifier.UnscopedElement) {
    EmojiSearchWidgetFactoryOwner.apply(EmojiSearch, value, element)
    RedwoodLayoutWidgetFactoryOwner.apply(RedwoodLayout, value, element)
    RedwoodLazyLayoutWidgetFactoryOwner.apply(RedwoodLazyLayout, value, element)
  }
}

/**
 * @suppress For generated code usage only.
 */
@RedwoodCodegenApi
public interface EmojiSearchWidgetFactoryOwner<W : Any> : WidgetFactoryOwner<W> {
  public val EmojiSearch: EmojiSearchWidgetFactory<W>

  public companion object {
    public fun <W : Any> apply(
      factory: EmojiSearchWidgetFactory<W>,
      `value`: W,
      element: Modifier.UnscopedElement,
    ) {
      when (element) {
        is Reuse -> factory.Reuse(value, element)
      }
    }
  }
}
