internal class FlexImpl(
  override val `value`: Double,
) : Flex {
  override fun equals(other: Any?): Boolean = other is Flex
  && other.`value` == `value`

  override fun hashCode(): Int {
    var hash = 17
    hash = 31 * hash + `value`.hashCode()
    return hash
  }

  override fun toString(): String = """Flex(value=$value)"""
}

