@file:Suppress("DEPRECATION", "OVERRIDE_DEPRECATION")

package com.example.redwood.emojisearch.compose

import androidx.compose.runtime.Composable
import app.cash.redwood.Modifier
import app.cash.redwood.RedwoodCodegenApi
import app.cash.redwood.compose.RedwoodComposeNode
import app.cash.redwood.compose.WidgetNode
import com.example.redwood.emojisearch.widget.EmojiSearchWidgetFactoryOwner
import com.example.redwood.emojisearch.widget.TextInput
import example.values.TextFieldState
import kotlin.Any
import kotlin.OptIn
import kotlin.String
import kotlin.Suppress
import kotlin.Unit

@Composable
@OptIn(RedwoodCodegenApi::class)
public fun TextInput(
  state: TextFieldState = TextFieldState(),
  hint: String = "",
  onChange: ((TextFieldState) -> Unit)? = null,
  modifier: Modifier = Modifier,
) {
  RedwoodComposeNode<EmojiSearchWidgetFactoryOwner<Any>, TextInput<Any>, Any>(
      factory = { it.EmojiSearch.TextInput() },
      update = {
        set(state) { recordChanged(); widget.state(it) }
        set(hint) { recordChanged(); widget.hint(it) }
        set(onChange) { recordChanged(); widget.onChange(it) }
        set(modifier, WidgetNode.SetModifiers)
      },
      content = {
      },
      )
}
